.App {
  font-family: "Palanquin", sans-serif;
  background: rgb(2,0,36);
  background: linear-gradient(39deg, rgba(2,0,36,1) 0%, rgba(54,54,120,1) 54%, rgba(40,44,52,1) 100%);
  min-height: 100vh;
  color: white;
}

header {
  font-size: 2.4rem;
  font-weight: 600;
  padding-left: 1.8rem;
  padding-top: 1rem;
}

section {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

section img {
  max-width: 16rem;
  margin-bottom: 1.2rem;
}

footer {
  position: fixed;
  width: 14rem;
  left: 50%;
  bottom: 2rem;
  display: flex;
  justify-content: space-between;
  transform: translateX(-50%);
}

footer a {
  font-size: 3.5rem;
}

footer a:hover {
  transform: scale(1.3);
  transition: all 0.5s;
}

footer a svg {
  color: white;
}
